/*
 *  Featured collections
 *    Styles for the featured collections section.
 *
 *  Version:
 *    1.0.0 - 2021/09/13
 */

.c-featured-collections {
  position: relative;
  background-color: $s-color--background-subdued;
  padding: 0 3.75rem 4.375rem;
  overflow: hidden;

  &.is-alt-bg {
    background-color: $s-color--background;
  }

  // List
  &__list {
    position: relative;
    margin: 0 0 3.125rem;
    padding: 0;
    list-style: none;
    z-index: $s-z--text-overlay;
  }

  &__item {
    text-align: center;
    margin-bottom: 1rem;

    &:last-child {
      margin-bottom: 0;
    }
    // Default "hover" on first link as per client's request
    &:first-of-type {
      .c-featured-collections__link {
        @include t-anim--transition((background-size,color), $ease: linear);
        background-image: linear-gradient(rgba($s-color--font-focus, 0.0), rgba($s-color--font-focus, 0.0)), linear-gradient($s-color--font-focus, $s-color--font-focus);
        background-size: 100% 0.25rem, 0 0.25rem;
        background-position: 100% 100%, 0 88%;
        background-repeat: no-repeat;
      }

      .c-featured-collections__link.is-active {
        background-size: 0 0.25rem, 100% 0.25rem;
        color: $s-color--font-focus;
      }
    }
  }

  // CTA
  &__cta-container {
    text-align: center;
  }

  // Features
  // Hide all the feature images by default
  [class*="feature-"] {
    position: absolute;
    z-index: $s-z--reset;
  }

  [class*="feature-"] &__image-container {
    opacity: 0;

    &:first-child { opacity: 1; }
  }

  [class*="feature-"] &__image {
    @include t-anim--transition(opacity, $s-anim--time-med, linear);
    opacity: 0;
    pointer-events: none;

    &.is-visible {
      opacity: 1;
      pointer-events: initial;
    }

    &-container {
      opacity: 1;
    }
  }

  &__feature-1 {
    top: 1.875rem;
    right: -2.25rem;
    width: 7.9375rem;
    display: flex;
    justify-content: flex-end;

    .c-featured-collections {
      &__image {
        &-wrapper {
          width: 5.7281rem;
        }

        &-container {
          padding-bottom: 139.56%;
        }
      }

      &__illustration {
        position:absolute;
        top: 100%;
        right: -0.25rem;
        transform: translateY(-2.0625rem);
        width: 7.9375rem;
        height: 6.875rem;
        z-index: $s-z--text-overlay;
        opacity: 0;

        &.is-visible  {
          opacity: 1;
        }

        &-background {
          position: absolute;
          right: 0;
          top: 100%;
          transform: translateY(-2.375rem);
          width: 5.375rem;
          height: 5.375rem;
          z-index: $s-z--reset;
        }
      }
    }
  }

  &__feature-2 {
    top: 8.625rem;
    left: -0.75rem;
    width: 5.4375rem;
    text-align: right;

    .c-featured-collections {
      &__image {
        &-wrapper {
          display: inline-block;
          width: 3.5rem;
        }

        &-container {
          padding-bottom: 117.16%;
        }
      }

      &__illustration {
        position: absolute;
        width: 3.9375rem;
        height: 3.6825rem;
        margin-top: -0.625rem;
        z-index: $s-z--text-overlay;
        opacity: 0;

        &.is-visible {
          opacity: 1;
        }
      }
    }
  }

  &__feature-3 {
    top: 19.75rem;
    left: -0.0625rem;
    width: 4.0575rem;

    .c-featured-collections {
      &__image {
        &-wrapper {
          width: 100%;
        }

        &-container {
          padding-bottom: 117.067%;
        }
      }
    }
  }

  &__link {
    @include t-anim--transition((background-size,color), $ease: linear);
    background-image: linear-gradient(rgba($s-color--font-focus, 0.0), rgba($s-color--font-focus, 0.0)), linear-gradient($s-color--font-focus, $s-color--font-focus);
    background-size: 100% 0.25rem, 0 0.25rem;
    background-position: 100% 100%, 0 88%;
    background-repeat: no-repeat;

    &:hover {
      background-size: 0 0.25rem, 100% 0.25rem;
      color: $s-color--font-focus;
    }
  }

  /*** Media Queries ***/

  @media screen and (hover: hover) and (min-width: #{$s-screen--medium}) {
    // Revert to initial state
    &__item {
      &:first-of-type {
        .c-featured-collections__link:hover {
          background-size: 0 0.25rem, 100% 0.25rem;
          color: $s-color--font-focus;
        }
      }
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 2.6875rem #{'min(6%, 7.5rem)'} 8.5rem;

    &__list  {
      max-width: 26.25rem;
      margin: 0 auto 3.125rem;
    }

    &__feature-1 {
      top: 0;
      right: 2.6563rem;
      width: 20.125rem;

      .c-featured-collections {
        &__image-wrapper {
          width: 15.625rem;
        }

        &__illustration {
          right: initial;
          left: -0.5rem;
          width: 18.125rem;
          height: auto;
          transform: translateY(-4.625rem);

          &-background {
            right: initial;
            left: 4rem;
            width: 11.6875rem;
            height: 11.625rem;
            transform: translateY(-3.875rem);
          }
        }
      }
    }

    &__feature-2 {
      top: 2.75rem;
      left: 6.4688rem;
      width: 14.375rem;

      .c-featured-collections {
        &__image-wrapper {
          width: 9.8438rem;
        }

        &__illustration {
          width: 6.625rem;
          height: 6.125rem;
          margin-top: -3.625rem;
        }
      }

    }

    &__feature-3 {
      top: 27.625rem;
      left: 3.7188rem;
      width: 11.375rem;
    }
  }

  @media screen and (hover: hover) and (min-width: #{$s-screen--xlarge}) {
    &__link {
      background-position: 100% 100%, 0 84%;
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 5.375rem #{'min(6%, 7.5rem)'} 12.5rem;

    &__list {
      max-width: 47.25rem;
    }

    &__item {
      margin-bottom: 1.5rem;
    }

    &__feature-1 {
      width: 32.3125rem;
      right: #{'min(6.05%, 7.5625rem)'};

      .c-featured-collections {
        &__image-wrapper {
          width: 25.5625rem;
        }

        &__illustration {
          left: -1.9375rem;
          width: 28.25rem;
          height: 24.5rem;
          transform: translateY(-7.8125rem);

          &-background {
            left: 6.125rem;
            width: 18rem;
            height: 18rem;
            transform: translateY(-6.875rem);
          }
        }
      }
    }

    &__feature-2 {
      top: 5.5rem;
      left: #{'min(10.8%, 13.5rem)'};
      width: 29.0625rem;

      .c-featured-collections {
        &__image-wrapper {
          width: 16.25rem;
        }

        &__illustration {
          width: 15.3125rem;
          height: 14.3125rem;
          margin-top: -8.375rem;
          transform: translateX(0.1875rem);
        }
      }
    }

    &__feature-3 {
      top: 35.5rem;
      left: #{'min(6%, 7.5rem)'};
      width: 18.8rem;
    }
  }
}
