/*
 *  Z-Index Settings
 *    Provides a manageable Z Index order list so that elements don't overflow
 *    when they shouldn't.
 *
 *  Version:
 *    2.0.0 - 2020/10/19
 */

$s-z--reset: 0;
$s-z--text-overlay: 5;
$s-z--illustration-overlay: 10;
$s-z--mobile-menu: 100;
$s-z--overlay: 150;
$s-z--sticky-header: 999997; // Insanely high to beat app widgets
$s-z--filter-drawer: $s-z--sticky-header + 1;
$s-z--drawer-background:  $s-z--filter-drawer + 1;
$s-z--cart-drawer: $s-z--drawer-background + 1;
$s-z--cart-drawer-tooltip: $s-z--cart-drawer + 1;